@import "src-doc/doc.global.scss";







































































.demo-virtual-list-container {
    width: 300px;
    height: 400px;
    background-color: #f6f6f6;

    .demo-virtual-list-item {
        display: block;
        width: 100%;
        height: 40px;

        .seq {
            width: 40px;
            background-color: rgba(0, 0, 0, 0.5);
            color: white;
            height: 100%;
            overflow: hidden;
            display: inline-flex;
            align-items: center;
            justify-content: center;
        }

        .content {
            width: calc(100% - 40px);
            float: right;
            height: 100%;
            padding: 0 12px;
            display: flex;
            justify-content: center;
            flex-direction: column;

            .label {
                font-size: 12px;
                display: flex;
                justify-content: space-between;
            }

            .star {
                text-align: right;
                color: white;
            }
        }
    }
}
